@import '_normalize.scss';
@import '_variables.scss';
@import '_sprite.scss';
@import '_mobile.scss';

// desktop
@media screen and (min-device-width: 960px) {
  html {
    font-size: 16px;
    height: 100%;
  }

  body {
    background: $bgColor;
    font-family: $fontFamily;
    color: $mainColor;
    min-height: 100%;
    margin: 0;
    display: flex;
    flex-direction: column;
  }

  .wrapper {
    width: 960px;
    min-height: 5px;
    margin: 0 auto;
    background-color: $bgColor;
  }

  .remove-block {
    display: none;
  }

  // header

  .header {
    width: 960px;
    height: 230px;
    padding-bottom: 26px;
    &__bglogo {
      width: 960px;
      height: 230px;
      background-image: url(../img/bglogospray.png);
    }
    &__logo {
      width: 960px;
      height: 230px;
      background-image: url(../img/logo.png);
    }
  }

  .menu {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-bottom: 10px;

    &__projects {
      width: 120px;
      height: 50px;
      @include sprite($projects1);
    }

    &__projects:hover {
      @include sprite($projects2);
    }

    &__about {
      width: 120px;
      height: 50px;
      @include sprite($about1);
    }

    &__about:hover {
      @include sprite($about2);
    }

    &__shop {
      width: 120px;
      height: 50px;
      @include sprite($shop1);
    }

    &__shop:hover {
      @include sprite($shop2);
    }

    &__photos {
      width: 120px;
      height: 50px;
      @include sprite($photos1);
    }

    &__photos:hover {
      @include sprite($photos2);
    }

    &__special {
      width: 120px;
      height: 50px;
      @include sprite($special1);
    }

    &__special:hover {
      @include sprite($special2);
    }

    &__contacts {
      width: 120px;
      height: 50px;
      @include sprite($contacts1);
    }

    &__contacts:hover {
      @include sprite($contacts2);
    }
  }
  // for mobile
  .nav-burger {
    display: none;
    &__checkbox {
      display: none;
    }
  }
  .nav {
    display: none;
  }

  // content

  .post {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 940px;
    height: 356px;
    margin: 0 auto;
    padding: 20px 10px;
    background-color: $bgColor;
    margin-bottom: 20px;
    border: solid 2px $postBorder;
    border-radius: 5px;
    &__image {
      width: 468px;
      height: 316px;
    }
    &__content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      width: 400px;
      height: 316px;
    }
  }

  .content {
    &__title {
      margin-top: 20px;
      font-family: $titleFontFamily;
      font-size: 28px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.07;
      letter-spacing: 0.8px;
      text-align: center;
      color: $mainColor;
    }
    &__text {
      font-family: $fontFamily;
      font-size: 16px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.25;
      letter-spacing: 0.5px;
      text-align: left;
      color: $textContent;
    }
    &__button {
      width: 300px;
      height: 40px;
      background-color: $mainColor;
      border: solid 2px $mainColor;
      font-family: $titleFontFamily;
      font-size: 16px;
      font-weight: bold;
      text-decoration: none;
      line-height: 1.25;
      letter-spacing: 0.5px;
      text-align: center;
      padding-top: 7px;
      color: $bgColor;
      border-radius: 5px;
    }
    &__button:hover {
      border: solid 2px $blockColor;
      background-color: $blockColor;
      color: $bgColor;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    }
  }

  .paginator {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    width: 980px;
    height: 50px;
    &__button {
      width: 180px;
      height: 30px;
      color: $mainColor;
      font-family: $titleFontFamily;
      text-decoration: none;
      font-size: 20px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      text-align: center;
    }
  }

  .disabled {
    color: $bgColor;
    cursor: default;
  }

  .hidden {
    display: none;
  }

  // footer

  .footer {
    width: 960px;
    height: 100px;
    margin-bottom: 5px;
    &__stripes {
      width: 960px;
      height: 50px;
    }
    &__logo {
      max-width: 960px;
      height: 15px;
      background-color: $blockColor;
      margin-bottom: 10px;
    }
    &__disclamer {
      height: 12px;
      font-family: $titleFontFamily;
      font-size: 13px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.54;
      letter-spacing: 0.4px;
      text-align: center;
      color: #a6a6a6;
    }
  }

  // about page
  .about {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 940px;
    min-height: 356px;
    margin: 10px auto 0;
    background-color: $bgColor;
    &__photo {
      margin: 0 auto;
      width: 800px;
      height: 530px;
      background-image: url(../img/about.JPG);
    }
    &__text {
      margin: 10px auto;
      font-size: 18px;
      width: 620px;
      min-height: 300px;
    }
    &__video {
      margin: 10px auto;
      width: 560px;
      height: 315px;
    }
  }

  // contacts
  .contacts {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    width: 300px;
    min-height: 100px;
    &__email {
      color: $mainColor;
      font-family: $titleFontFamily;
      text-decoration: none;
      font-size: 20px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      text-align: center;
      cursor: pointer;
    }
    &__instagram {
      color: $mainColor;
      font-family: $titleFontFamily;
      text-decoration: none;
      font-size: 20px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      text-align: center;
      cursor: pointer;
    }
  }

  // post-template
  // collection
  .collection {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 960px;
    min-height: 356px;
    padding: 20px 10px;
    background-color: $bgColor;
    margin-bottom: 20px;
    &__title {
      width: 240px;
      min-height: 30px;
      margin: 20px 0 10px;
      font-family: $titleFontFamily;
      font-size: 28px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.07;
      letter-spacing: 0.8px;
      text-align: center;
      color: $mainColor;
    }
    &__gallery {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-around;
      align-items: center;
      min-width: 959px;
      min-height: 316px;
      &-special {
        max-width: 640px;
        min-height: 316px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
      }
    }
    &__intro {
      width: 620px;
      min-height: 20px;
      margin-bottom: 10px;
      font-family: $fontFamily;
      font-size: 18px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.25;
      letter-spacing: 0.5px;
      text-align: left;
      color: #000;
    }
    &__qa {
      width: 620px;
      min-height: 20px;
      margin-bottom: 10px;
      display: flex;
      flex-direction: column;
    }
  }

  .qa {
    &__question {
      margin-bottom: 10px;
      font-family: $titleFontFamily;
      font-size: 18px;
      font-weight: bold;
      line-height: 1.25;
      text-align: left;
      color: #000;
    }
    &__answer {
      margin: 0;
      font-family: $fontFamily;
      font-size: 18px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.25;
      letter-spacing: 0.5px;
      text-align: left;
      color: #606060;
    }
    &__contacts {
      margin: 10px;
      align-self: center;
      color: $linkColor;
      font-family: $titleFontFamily;
      text-decoration: none;
      font-size: 20px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      text-align: center;
      cursor: pointer;
    }
    &__video {
      margin-top: 10px;
      width: 560px;
      height: 315px;
    }
  }

  .gallery__image {
    margin-bottom: 10px;
    max-width: 300px;
    max-height: 300px;
    &--main {
      margin-bottom: 10px;
      max-width: 800px;
      max-height: 600px;
    }
  }

  .slide-in {
    opacity: 0;
    transition: all 0.5s;
  }

  .gallery__image--main.slide-in {
    transform: translateX(-30%) scale(0.95);
  }

  .slide-in.active {
    opacity: 1;
    transform: translateX(0%) scale(1);
  }

  .gallery__image:hover {
    cursor: pointer;
  }

  // preloader
  .preloader {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: $mainColor;
    z-index: 2;
    transition: 1s all;
    opacity: 1;
    visibility: visible;
  }

  .loader {
    width: 75px;
    height: 75px;
    border: 10px solid $bgColor;
    border-radius: 50%;
    border-top-color: $mainColor;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    animation: 1s spin infinite linear;
  }

  .done {
    opacity: 0;
    visibility: hidden;
  }

  @keyframes spin {
    from {
      transform: translate(-50%, -50%) rotate(0deg);
    }
    to {
      transform: translate(-50%, -50%) rotate(360deg);
    }
  }

  // collaborate
  .collaborate {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    min-height: 50px;
    min-width: 100%;
    background: $linkColor;
    &__text {
      font-family: $titleFontFamily;
      font-size: 18px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.25;
      letter-spacing: 0.5px;
      text-align: center;
      text-decoration: none;
      color: $bgColor;
    }
  }

  // shop
  .shop {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    width: 300px;
    min-height: 100px;
    &__soon {
      color: $mainColor;
      font-family: $titleFontFamily;
      text-decoration: none;
      font-size: 20px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      text-align: center;
      cursor: pointer;
    }
  }

  // image viewer
  .overlay-close {
    background: none;
    color: black;
    border: 0;
  }

  .overlay {
    position: fixed;
    background: rgba(0, 0, 0, 0.9);
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: none;
    z-index: 2;
  }

  .overlay.open {
    display: grid;
    align-items: center;
    justify-items: center;
  }

  .overlay-inner {
    display: flex;
    flex-direction: column;
    background: white;
    max-width: 840px;
    padding: 10px 20px 20px;
  }

  .overlay-close {
    align-self: flex-end;
    height: 30px;
    width: 80px;
    background: $linkColor;
    border-radius: 10px;
    color: $bgColor;
    border: 0;
    margin-bottom: 10px;
  }

  .overlay-close:hover {
    cursor: pointer;
  }

  .overlay-image {
    max-width: 800px;
    max-height: 600px;
  }
}
