// mobile version
@media screen and (max-device-width: 958px) {
  html {
    font-size: 16px;
    height: 100%;
  }

  body {
    background: $bgColor;
    font-family: $fontFamily;
    color: $mainColor;
    min-height: 100%;
    margin: 0;
    display: flex;
    flex-direction: column;
  }

  .wrapper {
    max-width: 960px;
  }

  .remove-block {
    display: none;
  }

  // header block
  header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: $bgColor;
    height: 70px;
    min-width: 100%;
  }

  .header {
    width: 50px;
    height: 50px;
    margin: 10px;
    &__bglogo {
      width: 115px;
      height: 50px;
      background-image: url(../img/somevan50.png);
    }
  }

  .menu {
    width: 50px;
    height: 50px;
    margin: 10px;
    background-color: $bgColor;
  }

  .nav {
    width: 100%;
    box-shadow: inset 0 5px rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 70px;
    left: 0;
    z-index: 100;
    display: none;
  }

  .nav__item {
    display: block;
    height: 56px;
    font-weight: bold;
    color: #fff;
    font-family: $titleFontFamily;
    font-size: 14px;
    line-height: 56px;
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    background-color: #3d3d3f;
    transition: background-color 0.1s linear, color 0.1s linear;
  }

  /* styled menu elements */
  .nav__item:not(:first-of-type) {
    border-top: 1px solid rgba(255, 255, 255, 0.2);
  }

  .nav__item:hover {
    background-color: #3d3d3f;
  }

  .nav__item:active {
    background-color: black;
    color: #fff;
  }

  .nav-burger {
    display: block;
    width: 30px;
    height: 30px;
    cursor: pointer;
    color: transparent;
    font-size: 0;
    background: url('../img/icon-menu.svg') no-repeat;
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 1;
  }

  .nav-burger__checkbox {
    display: none;
  }

  .nav-burger__checkbox:checked + .nav-burger {
    background-image: url('../img/icon-cross.svg');
  }

  .nav-burger__checkbox:checked ~ .nav {
    display: block;
  }

  // content

  .post {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
    max-width: 959px;
    min-height: 223px;
    margin: 5px;
    padding: 10px;
    background-color: $bgColor;
    margin-bottom: 20px;
    border: solid 2px $postBorder;
    &__image {
      width: 300px;
      height: 203px;
    }
    &__content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      width: 320px;
      min-height: 203px;
      font-size: 13px;
    }
  }

  .content {
    &__title {
      margin-top: 5px;
      font-family: $titleFontFamily;
      font-size: 16px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.07;
      letter-spacing: 0.8px;
      text-align: left;
      color: $mainColor;
    }
    &__text {
      font-family: $fontFamily;
      font-size: 13px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.25;
      letter-spacing: 0.5px;
      text-align: left;
      color: #606060;
    }
    &__button {
      width: 300px;
      height: 40px;
      background-color: $mainColor;
      border: solid 2px $mainColor;
      font-family: $titleFontFamily;
      font-size: 16px;
      font-weight: bold;
      text-decoration: none;
      line-height: 1.25;
      letter-spacing: 0.5px;
      text-align: center;
      padding-top: 7px;
      color: $bgColor;
      border-radius: 5px;
    }
    &__button:hover {
      border: solid 2px $blockColor;
      background-color: $blockColor;
      color: $bgColor;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    }
  }

  .paginator {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    max-width: 980px;
    height: 50px;
    &__button {
      width: 120px;
      height: 30px;
      color: $mainColor;
      font-family: $titleFontFamily;
      text-decoration: none;
      font-size: 16px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      text-align: center;
    }
  }

  .disabled {
    color: $bgColor;
    cursor: default;
  }

  .hidden {
    display: none;
  }

  // footer

  .footer {
    max-width: 959px;
    height: 100px;
    margin-bottom: 10px;
    &__stripes {
      max-width: 960px;
      height: 50px;
    }
    &__logo {
      max-width: 960px;
      height: 15px;
      background-color: $blockColor;
      margin-bottom: 10px;
    }
    &__disclamer {
      padding: 0 5px;
      font-family: $titleFontFamily;
      font-size: 13px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.54;
      letter-spacing: 0.4px;
      text-align: center;
      color: #a6a6a6;
    }
  }

  // about page
  .about {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
    max-width: 959px;
    min-height: 223px;
    padding: 10px;
    background-color: $bgColor;
    margin-bottom: 10px;
    &__photo {
      width: 300px;
      height: 203px;
      background-image: url(../img/about.JPG);
      background-size: 100%;
      margin-bottom: 10px;
    }
    &__text {
      width: 320px;
      padding: 0 10px;
      min-height: 203px;
      font-size: 13px;
    }
    &__video {
      margin-top: 10px;
      width: 560px;
      height: 315px;
    }
  }

  // contacts
  .contacts {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: 10px auto;
    width: 300px;
    min-height: 50px;
    &__email {
      color: $mainColor;
      font-family: $titleFontFamily;
      text-decoration: none;
      font-size: 20px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      text-align: center;
      cursor: pointer;
    }
    &__instagram {
      color: $mainColor;
      font-family: $titleFontFamily;
      text-decoration: none;
      font-size: 20px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      text-align: center;
      cursor: pointer;
    }
  }

  // post-template
  // collection
  .collection {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    max-width: 959px;
    min-height: 356px;
    padding: 10px;
    background-color: $bgColor;
    margin-bottom: 10px;
    &__title {
      min-width: 240px;
      min-height: 30px;
      margin: 20px 10px 10px;
      font-family: $titleFontFamily;
      font-size: 28px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.07;
      letter-spacing: 0.8px;
      text-align: center;
      color: $mainColor;
    }
    &__gallery {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-around;
      align-items: center;
      max-width: 959px;
      min-height: 200px;
      &-special {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        max-width: 959px;
        min-height: 316px;
      }
    }
    &__intro {
      max-width: 620px;
      min-height: 20px;
      margin-bottom: 10px;
      font-family: $fontFamily;
      font-size: 18px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.25;
      letter-spacing: 0.5px;
      text-align: left;
      color: #000;
    }
    &__qa {
      max-width: 620px;
      min-height: 20px;
      margin-bottom: 10px;
      display: flex;
      flex-direction: column;
    }
  }

  .qa {
    &__question {
      margin-bottom: 10px;
      font-family: $titleFontFamily;
      font-size: 18px;
      font-weight: bold;
      line-height: 1.25;
      text-align: left;
      color: #000;
    }
    &__answer {
      margin: 0;
      font-family: $fontFamily;
      font-size: 18px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.25;
      letter-spacing: 0.5px;
      text-align: left;
      color: #606060;
    }
    &__contacts {
      margin: 10px 5px;
      align-self: center;
      color: $linkColor;
      font-family: $titleFontFamily;
      text-decoration: none;
      font-size: 18px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      text-align: center;
      cursor: pointer;
    }
    &__video {
      margin-top: 7px;
      height: 0;
      position: relative;
      padding-bottom: 56.25%;
    }
    &__video--iframe {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }
  }

  .gallery__image {
    margin-bottom: 10px;
    max-width: 300px;
    max-height: 300px;
    &--main {
      margin-bottom: 10px;
      display: block;
      max-width: 100%;
    }
  }

  .slide-in {
    opacity: 0;
    transition: all 0.5s;
  }

  .gallery__image--main.slide-in {
    transform: translateX(-30%) scale(0.95);
  }

  .slide-in.active {
    opacity: 1;
    transform: translateX(0%) scale(1);
  }

  // preloader
  .preloader {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: $mainColor;
    z-index: 2;
    transition: 1s all;
    opacity: 1;
    visibility: visible;
  }

  .loader {
    width: 75px;
    height: 75px;
    border: 10px solid $blockColor;
    border-radius: 50%;
    border-top-color: $linkColor;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    animation: 1s spin infinite linear;
  }

  .done {
    opacity: 0;
    visibility: hidden;
  }

  @keyframes spin {
    from {
      transform: translate(-50%, -50%) rotate(0deg);
    }
    to {
      transform: translate(-50%, -50%) rotate(360deg);
    }
  }

  // collaborate
  .collaborate {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    min-height: 50px;
    min-width: 100%;
    background: $linkColor;
    &__text {
      font-family: $titleFontFamily;
      font-size: 14px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.25;
      letter-spacing: 0.5px;
      text-align: center;
      text-decoration: none;
      color: $bgColor;
    }
  }

  // shop
  .shop {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    width: 300px;
    min-height: 100px;
    &__soon {
      color: $mainColor;
      font-family: $titleFontFamily;
      text-decoration: none;
      font-size: 20px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      text-align: center;
      cursor: pointer;
    }
  }

  // image viewer
  .overlay {
    display: none;
  }
}
